import React from 'react';
import './App.css';
import { Route, Switch } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import Login from './pages/Login';
import Profile from './pages/Profile';
import RecipeInProgress from './pages/RecipeInProgress';
import DoneRecipes from './pages/DoneRecipes';
import FavoriteRecipes from './pages/FavoriteRecipes';
import RecipeDetails from './pages/RecipeDetails';
import Provider from './context/myProvider';
import Recipes from './pages/Recipes';

function App() {
  return (
    <Provider>
      <Switch>
        <Route exact path="/18-Project-Recipes-App/" component={ Login } />
        <Route exact path="/18-Project-Recipes-App/meals" component={ Recipes } />
        <Route exact path="/18-Project-Recipes-App/drinks" component={ Recipes } />
        <Route
          exact
          path="/18-Project-Recipes-App/meals/:id"
          component={ RecipeDetails }
        />
        <Route
          exact
          path="/18-Project-Recipes-App/drinks/:id"
          component={ RecipeDetails }
        />
        <Route
          exact
          path="/18-Project-Recipes-App/meals/:id/in-progress"
          component={ RecipeInProgress }
        />
        <Route
          exact
          path="/18-Project-Recipes-App/drinks/:id/in-progress"
          component={ RecipeInProgress }
        />
        <Route exact path="/18-Project-Recipes-App/profile" component={ Profile } />
        <Route
          exact
          path="/18-Project-Recipes-App/done-recipes"
          component={ DoneRecipes }
        />
        <Route
          exact
          path="/18-Project-Recipes-App/favorite-recipes"
          component={ FavoriteRecipes }
        />
      </Switch>
    </Provider>
  );
}

export default App;
